* {
  margin: 0;
  padding: 0;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
  border: none;
  color: #000;
}
a:hover {
  text-decoration: none;
}
body {
  width: 100%;
  margin: 0 auto;
  color: #000;
  font: 400 14px 'Arimo', sans-serif;
}
.width {
  margin: 0 auto;
  width: 70%;
}
#header {
  background: #f7f7f7;
}
.header {
  padding: 25px 0;
}
.header__title {
  float: left;
}
.header__title img {
  width: 100%;
  height: 100%;
}
.header__call_back {
  float: right;
  font: 400 18px 'Arimo', sans-serif;
  color: #767676;
  margin-top: 5px;
}
.header__call_back .header__call_back_tel {
  float: left;
}
.header__call_back a {
  float: right;
  text-transform: uppercase;
  color: #fff;
  font-size: 15px;
  background: #282c37;
  padding: 11px 32px;
  margin-left: 25px;
}
.header__call_back a:hover {
  background: #ccc;
}
.header__call_back a:active {
  background: #8f8989;
}
#header__menu {
  background: #282c37;
  position: fixed;
  z-index: 1000;
  width: 100%;
  margin: 0 auto;
}
.hide {
  display: none;
}
.header__menu-wrap {
  max-width: 900px;
  padding: 35px 0;
}
.menu__list {
  text-align: justify;
  font-size: 0;
}
.menu__list:after {
  content: "";
  display: inline-block;
  width: 100%;
  overflow: hidden;
}
.menu__list li:last-child:before {
  display: none;
}
.menu__list-item {
  display: inline-block;
  vertical-align: top;
  position: relative;
}
.menu__list-item:before {
  position: absolute;
  content: "";
  border-right: 1px solid #424757;
  top: 0;
  right: -60px;
  width: 1px;
  height: 100%;
}
.menu__list-item a {
  color: #fff;
  font-size: 15px;
  text-transform: uppercase;
}
.menu__list-item a.active {
  text-decoration: underline;
}
.menu__list-item a:hover {
  text-decoration: underline;
}
#main {
  height: 537px;
  position: relative;
}
.main__slide {
  background-position: center center;
  background-size: cover;
}
.main__slider {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.main__slide {
  width: 100%;
  height: 100%;
}
#content_wrap {
  margin-top: 40px;
}
.breadcrumbs {
  margin-bottom: 40px;
}
.breadcrumbs ul {
  font-size: 0;
  text-align: left;
}
.breadcrumbs ul li:last-child:before {
  display: none;
}
.breadcrumbs ul li {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin-right: 30px;
}
.breadcrumbs ul li:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 4px solid #7e7e7e;
  border-bottom: 4px solid transparent;
  top: 50%;
  margin-top: -4px;
  right: -15px;
}
.breadcrumbs ul li a {
  color: #282c37;
  font-size: 14px;
  text-transform: capitalize;
}
.breadcrumbs ul li a:hover {
  text-decoration: underline;
  color: #767676;
}
.breadcrumbs ul li a.active {
  color: #767676;
}
#sidebar {
  float: left;
  width: 18.36734693877551%;
}
#sidebar dd {
  display: none;
  margin: -10px 0 10px 25px;
}
#sidebar dd a {
  color: #767676;
  margin-bottom: 15px;
  display: block;
  font-size: 14px;
  position: relative;
}
#sidebar dd a:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  background: #767676;
  border-radius: 50px;
  top: 50%;
  margin-top: -2px;
  left: -10px;
}
#sidebar dd a:hover {
  color: #282c37;
  text-decoration: underline;
}
#sidebar dd a.active {
  color: #282c37;
  text-decoration: underline;
}
#catalog {
  float: right;
  width: 78.57142857142857%;
}
.sidebar__list {
  font: 400 18px 'Arimo', sans-serif;
  margin-bottom: 20px;
}
.sidebar__list a {
  font-size: 16px;
  color: #767676;
}
.sidebar__list a:hover:hover {
  color: #282c37;
  text-decoration: underline;
}
.sidebar__list a.active {
  color: #282c37;
  text-decoration: underline;
}
.download_price {
  margin-top: 65px;
  display: inline-block;
  color: #282c37;
  font: 600 16px 'Arimo', sans-serif;
  text-transform: capitalize;
  position: relative;
  padding-left: 65px;
}
.download_price:before {
  position: absolute;
  content: "";
  background: url(../images/price-ico.png);
  width: 42px;
  height: 40px;
  top: 50%;
  margin-top: -20px;
  left: 0;
}
.catalog__list {
  text-align: justify;
  font-size: 0;
}
.catalog__list:after {
  content: "";
  display: inline-block;
  width: 100%;
  overflow: hidden;
}
.catalog__list-item {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  width: 31%;
  font: 400 16px 'Arimo', sans-serif;
  color: #767676;
  text-transform: uppercase;
  border: 1px solid #e4e4e4;
  border-radius: 3px;
  margin-bottom: 3.3333%;
  text-align: center;
}
.catalog__list-item .item_cont {
  padding: 15px;
}
.catalog__list-item .price {
  color: #444;
  font: 600 28px 'Arimo', sans-serif;
  text-align: left;
  padding: 15px;
  border-top: 1px solid #e4e4e4;
  position: relative;
}
.catalog__list-item .price:before {
  content: "";
  position: absolute;
  background: url(../images/bag-ico.png);
  width: 28px;
  height: 21px;
  top: 50%;
  margin-top: -14px;
  right: 15px;
  cursor: pointer;
}
.catalog__list-item img {
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
}
.pagination {
  margin: 20px auto;
  width: 31%;
}
.pagination ul {
  text-align: justify;
}
.pagination ul:after {
  content: "";
  display: inline-block;
  width: 100%;
  overflow: hidden;
}
.pagination ul li {
  display: inline-block;
}
.pagination ul li a {
  font-size: 14px;
  color: #767676;
}
.pagination ul li a.active {
  color: #444;
  font-size: 18px;
}
.pagination ul li a:hover {
  text-decoration: underline;
}
#art {
  margin-top: 30px;
  border-top: 1px solid #777;
  padding-top: 40px;
  color: #767676;
  line-height: 21px;
  margin-bottom: 80px;
}
#art .art1 {
  float: left;
  width: 48%;
}
#art .art2 {
  float: right;
  width: 48%;
}
#footer {
  border-top: 1px solid #eaeaea;
}
.footer {
  padding-top: 40px;
  padding-bottom: 40px;
}
.footer__copyright-copy {
  float: left;
  width: 14.97959183673469%;
  color: #767676;
  line-height: 21px;
  text-transform: capitalize;
}
.footer__menu {
  float: right;
  width: 65.81632653061224%;
}
.footer__menu .header__menu-wrap {
  padding: 0;
}
.footer__menu ul li:before {
  display: none;
}
.footer__menu ul li a {
  color: #4c5059;
  font-size: 14px;
}
.footer__menu ul li a:hover {
  text-decoration: underline;
}
.footer__menu ul li a.active {
  text-decoration: none;
}
@keyframes hvr-wobble-skew {
  16.65% {
    transform: skew(-12deg);
  }
  33.3% {
    transform: skew(10deg);
  }
  49.95% {
    transform: skew(-6deg);
  }
  66.6% {
    transform: skew(4deg);
  }
  83.25% {
    transform: skew(-2deg);
  }
  100% {
    transform: skew(0);
  }
}
@keyframes slideInLeft {
  from {
    transform: translate3d(-150%, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInLeft2 {
  from {
    transform: translate3d(-200%, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInRight {
  from {
    transform: translate3d(150%, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }
  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes pulse2 {
  from {
    transform: scale3d(1, 1, 1);
  }
  50% {
    transform: scale3d(1.1, 1.1, 1.1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes slideInDown {
  from {
    transform: translate3d(0, -150%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInUp {
  from {
    transform: translate3d(0, 150%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@keyframes rotateIn {
  from {
    transform-origin: center;
    transform: rotate3d(0, 0, 1, -200deg);
    opacity: 0;
  }
  to {
    transform-origin: center;
    transform: none;
    opacity: 1;
  }
}
@keyframes rotateOut {
  from {
    -webkit-transform-origin: center;
    transform-origin: center;
    opacity: 1;
  }
  to {
    -webkit-transform-origin: center;
    transform-origin: center;
    -webkit-transform: rotate3d(0, 0, 1, 200deg);
    transform: rotate3d(0, 0, 1, 200deg);
    opacity: 0;
  }
}
#order__popup {
  display: none;
  text-align: left;
  width: 470px;
  padding: 30px 60px;
  background: #fff;
  margin: 0 auto;
  box-sizing: border-box;
  color: #535353;
}
#order__popup h3 {
  text-align: center;
  color: #535353;
  font: 700 24px 'Arimo', sans-serif;
  text-transform: uppercase;
  margin-bottom: 30px;
}
#order__popup .control:nth-child(4) label:after {
  font-size: 0;
}
#order__popup .control {
  margin-bottom: 20px;
}
#order__popup .control input,
#order__popup .control textarea {
  box-sizing: border-box;
  width: 100%;
  display: block;
  padding: 10px 15px;
  border: 1px solid #c3c3c3;
}
#order__popup .control input:focus,
#order__popup .control textarea:focus {
  border-color: yellow;
  box-shadow: 0 0 10px rgba(255, 136, 0, 0.6);
}
#order__popup .control input.error,
#order__popup .control textarea.error {
  border-color: #f00;
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.6);
}
#order__popup .control textarea {
  height: 140px;
  resize: none;
}
#order__popup .control label {
  display: inline-block;
  padding-bottom: 10px;
}
#order__popup .control label:after {
  content: "*";
  color: #c61015;
  margin-left: 3px;
}
#order__popup .send_app {
  text-align: right;
  width: 100%;
}
#order__popup .send_app input {
  padding: 10px 30px;
  text-decoration: none;
  text-transform: uppercase;
  background: #282c37;
  color: #fff;
  font: 700 18px 'Arimo', sans-serif;
  border-color: #343842;
  border: 1px solid #343842;
}
#order__popup .send_app input:hover {
  background: #444;
}
#order__popup .send_app input:active {
  background: #34363a;
}
#popup__tnx {
  display: none;
  text-align: center;
  width: 470px;
  padding: 60px;
  margin: 0 auto;
  box-sizing: border-box;
  background: #fff;
  color: #535353;
}
#popup__tnx h3 {
  text-align: center;
  font: 700 24px 'Arimo', sans-serif;
  text-transform: uppercase;
  margin-bottom: 30px;
}
#popup__tnx p {
  margin: 0 auto;
  font-size: 18px;
  width: 80%;
}
* {
  max-height: 30000px;
}
